.hero {
    background: var(--background-primary);
    padding-top: 2rem;
    padding-bottom: var(--py);
    text-align: center;
}

html {
    scroll-behavior: smooth;
}


.hero .h1 {
    margin-bottom: 1rem;
    line-height: 1.6;
    font-size: 4vw;
}

.hero b {
    color: var(--accent);
    font-weight: inherit;
}

.hero .h3 {
    color: var(--foreground-secondary);
    margin-bottom: 2rem;
}

.hero .btn-group {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 15px;
    text-align: center;
}

.hero .right {
    display: none;
}


.typewriter-text {
    display: inline-block;
    overflow: hidden;
    letter-spacing: 2px;
    animation: typing 4.5s steps(30, end) infinite, blink .5s step-end infinite;
    white-space: nowrap;
    font-size: 3.2vw;
    font-weight: 700;
    border-right: 4px solid orange;
    color: #38B0FF;
}

@media (min-width: 1024px) {

    .hero {
        text-align: left;
    }

    .hero .container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        gap: 50px;
    }

    .hero .btn-group {
        justify-content: start;
        gap: 30px;
    }

    .hero .right {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .hero .pattern-bg {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        height: 200px;
        background-size: contain;
        opacity: 0.2;
    }

    .hero .img-box {
        position: relative;
        z-index: 2;
    }

    .hero-img {
        width: 35vw;
        height: 38vw;
        object-fit: contain;
        transform: translate(10px, -10px);
    }

    .hero .shape {
        position: absolute;
        top: 50%;
        left: 50%;
        border-radius: 50%;
        transform: translate(-50%, -42%) rotate(-20deg);
    }

    .hero .shape-1 {
        background: var(--accent);
        width: 90%;
        height: 90%;
        z-index: -1;
    }

    .hero .shape-2 {
        width: 92%;
        height: 92%;
        box-shadow: inset 0 -30px 0 var(--action-primary);
        z-index: 2;
    }

}

@media (max-width: 524px) {
    .hero .h1 {
        margin-bottom: 1rem;
        line-height: 1.6;
        font-size: 7.5vw;
    }

    .typewriter-text {
        font-size: 18px;
    }

}



@keyframes typing {
    from {
        width: 0%
    }

    to {
        width: 100%
    }
}

@keyframes blink {
    from, to {
        border-color: transparent
    }

    50% {
        border-color: orange;
    }
}